.logo{
font-family: 'zodiac';
font-size: 100px;
margin: -10px;
cursor: pointer;
}
.application{
overflow: hidden;
}
.footer{
	position:fixed;
	left:0px;
	bottom:0px;
	height:30px;
	padding: 5px 0px 20px 0px;
	width:100%;
    background-color:rgba(53, 126, 221, 0.9);
	font-family: 'Patrick Hand', cursive;
	font-size: 20px;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
