.ZodiacInfo{
    background-color: rgba(0, 0, 0, 0.5);;
    color: white;
    padding: 10px;  
    overflow-x: hidden;
    height: 100vh;
}
.emojis{
    padding: 0px 5px;
    display: inline-flex;
    flex-direction:column;
}
.gobackbtn{
    margin:20px 0px;
    padding: 15px;
    background-color: #357edd;
    font-size: 18px;
    border-radius: 8px;
     cursor: pointer;
}
.listZodiac{
    list-style: none;
    text-align:center;
    display: grid;
    flex-direction: column;
    justify-content: center;
}
.listitem{
    padding: 5px;
    background-color:rgba(53, 126, 221, 0.4);
    width:100%;
    margin:5px;
    font-size: 18px;
    border-radius: 8px;
}


.datarow{
    display: inline-flex;
    flex-direction: row;
}
.textpos{
    margin: auto;
    width: 50%;
    padding: 40px;
}
.notfoundtxt{
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: center;
    margin: 20%;
